import React, { useState } from 'react';
import { useLocation } from 'react-router-dom';
import Home from "../acess/svg/home.svg";
import { Link } from 'react-router-dom';
import zalo from "../acess/zalo.jpg"
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';

function Contact() {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    let location = useLocation();

    return (
        <main className='bg-white'>
            <div className="flex flex-col justify-centera items-center">
                <div className='max-w-[1200px] w-full px-[20px] md:px-[20px]'>
                    <div className='w-full'>
                        <div className='w-full mt-[10px] border-[1px] border-solid border-[#eaeaea] flex flex-row justify-start items-center'>
                            <Link className='p-[10px]' to="/">
                                <img src={Home} alt='' className='w-[15px] h-[15px]'></img>
                            </Link>
                            <div></div>
                            <Link to={location.pathname} className='text-black no-underline'>
                                <div className='text-[12px] font-[400] cursor-pointer uppercase'>LIN H</div>
                            </Link>
                        </div>
                        <div className='flex flex-col md:flex-row w-full'>
                            <div className="w-full">
                                <div className='py-[20px] text-[20px] md:text-[24px] font-[700] uppercase'>LIN H</div>
                                <div className='w-full flex flex-col md:flex-row'>
                                    <div className='w-full mr-[20px]'>
                                        <p class="card-text">
                                            <input
                                                type="text"
                                                class="form-control"
                                                placeholder="H v tn"
                                                style={{ marginBottom: '10px' }}
                                            />
                                            <input
                                                type="text"
                                                class="form-control"
                                                placeholder="S in thoi"
                                                style={{ marginBottom: '10px' }}
                                            />
                                            <input
                                                type="text"
                                                class="form-control"
                                                placeholder="Email"
                                                style={{ marginBottom: '10px' }}
                                            />
                                            {/* <input
                                                type="text"
                                                class="form-control"
                                                placeholder="Cu chuyn ca bn"
                                                style={{ marginBottom: '10px' }}
                                            /> */}
                                            <button onClick={handleShow} class="btn" type="submit" className='text-white bg-[#BF211A] uppercase px-[10px] py-[10px] my-[10px]'>
                                                ng k
                                            </button>
                                        </p>
                                    </div>
                                    <div className='w-full py-[30px] md:py-0 mb-[20px]'>
                                        <div className='text-[16px] font-[700] mb-[10px]'>T vn</div>
                                        <div className='text-[14px]  mb-[10px]'>Email: srsgogakukyositsu@gmail.com</div>
                                        <div className='text-[14px] mb-[10px]'>
                                            Ch : life coach , chia s cch xem ch tay , phong thu theo trng phi Nht Bn, Gio dc ting Nht, phin dch dch thut ting Nht , Vit
                                        </div>
                                        <img style={{width: '200px', height: '200px'}} src={zalo} alt="qr" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">ng k thnh cng</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {/* <h4>Centered Modal</h4> */}
                    <h4>
                        Cm n bn  ng k, KAKOJP s sm phn hi
                    </h4>
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={handleClose}>ng</Button>
                </Modal.Footer>
            </Modal>
        </main>
    );
}

export default Contact;